import {Component, OnInit, OnDestroy, Input} from "@angular/core";
import {FormGroup, FormControl} from "@angular/forms";
import {Subject, Subscription} from "rxjs";
import { P } from "@angular/core/src/render3";
import * as _ from 'lodash';
import { MarkerModel } from "../../../../shared/models/marker.model";

@Component({
  selector: 'details-map',
  templateUrl: './details-map.component.html',
  styleUrls: ['./details-map.component.scss'],
})
export class DetailsMapComponent implements OnInit, OnDestroy {

  @Input() lotteryBuildings: any[];
  @Input() subways: string[];

  public buildingLocations: MarkerModel[] = [];
  public nearbyLocations: MarkerModel[] = [];

  public eventsSubject: Subject<any> = new Subject<any>();
  public nearbyPlaces: any[];
  public markers: any[];
  
  public mapFilterForm: FormGroup = new FormGroup({
    school: new FormControl(false),
    park: new FormControl(false),
    hospital: new FormControl(false),
    // police: new FormControl(false),
  });

  private mapFormSubscription: Subscription;

  public onUpdate(arg): void {
    this.eventsSubject.next(arg);
  }

  public selectAllFormValues(): void {
    Object.keys(this.mapFilterForm.controls)
    .forEach((key) =>
      this.mapFilterForm.get(key).setValue(true)
    );
  }

  public clearFormValues(): void {
    this.mapFilterForm.reset();
  }

  constructor() {}

  ngOnInit() {

    if (this.lotteryBuildings) {
      this.lotteryBuildings.forEach((b, i) => {
        let m = new MarkerModel();
        m.id = b.building.buildingId;
        m.index = i + 1;
        m.lat = b.building.latitude;
        m.lng = b.building.longitude;
        m.label = b.building.address;
        m.address = b.building.address;
        m.city = b.building.city;
        m.state = b.building.state;
        m.zip = b.building.zip;
        m.type = 'building';

        this.buildingLocations.push(m);

        if (b.building.nearbyPlaces) {
          b.building.nearbyPlaces.forEach((p, j) => {
            let n = new MarkerModel();
            n.id = p.nearbyPlaceId;
            n.lat = p.latitude;
            n.lng = p.longitude;
            n.label = p.nearbyPlaceName;
            n.type = p.placeType;
            n.trainName = p.trainName;

            this.nearbyLocations.push(n);
          });
        }

        // maps buildings to nearby place subways, then filters distinct
        this.subways = this.lotteryBuildings.map(lb => lb.building).map(b => b.nearbyPlaces.filter(n => n.trainName).map(n => n.trainName))
          .map(s => s.join().replace(/\s/, ''))
          .join(',').split(',')
          .filter((e, i, a) => a.indexOf(e) === i);

      });

      this.nearbyLocations = _.uniq(this.nearbyLocations);
    }

    this.mapFormSubscription = this.mapFilterForm.valueChanges.subscribe((value) => {
      this.onUpdate(value);
    });
  }

  ngOnDestroy() {
    this.mapFormSubscription.unsubscribe();
  }
}
