import { Component, Input, ChangeDetectorRef, Output, EventEmitter } from '@angular/core';
import * as _ from 'lodash';
import { NgxGalleryOptions, NgxGalleryImage } from '../../../ngx-gallery';

@Component({
  selector: 'details-greeter',
  templateUrl: './details-greeter.component.html',
  styleUrls: ['./details-greeter.component.scss']
})
export class DetailsGreeterComponent {
  @Input() lotteryName: string;
  @Input() lotteryBuildings: any;
  @Input() defaultImage: string;
  @Input() units: any[];
  @Input() endDate: string;
  @Input() daysLeft: string;
  @Output() onOpenGalleryEvent: EventEmitter<any> = new EventEmitter();

  minIncome: number;
  maxIncome: number;
  minHouseholdSize: number;
  maxHouseholdSize: number;

  @Input() galleryImages: NgxGalleryImage[] = [];

  ngOnInit() {
    console.log("UNITS: ", this.units);
    const allUnitIncomeRequirements = _.flatten(_.map(this.units, (item) => item.unitIncome));

    this.minHouseholdSize = _.get(_.minBy(allUnitIncomeRequirements, 'houseHoldSize'), 'houseHoldSize');
    this.maxHouseholdSize = _.get(_.maxBy(allUnitIncomeRequirements, 'houseHoldSize'), 'houseHoldSize');
    this.minIncome = _.get(_.minBy(allUnitIncomeRequirements, 'minimumIncome'), 'minimumIncome');
    this.maxIncome = _.get(_.maxBy(allUnitIncomeRequirements, 'maximumIncome'), 'maximumIncome');

    this.cdRef.markForCheck();
  }

  photoButtonClicked() {
    this.onOpenGalleryEvent.emit('open');
  }

  scrollTo(element: any) {

    const elem = document.querySelector('#' + element);
    if (elem) {
      elem.scrollIntoView({ behavior: 'smooth', block: 'center', inline: 'nearest' });
    }
  }
  
  constructor(private cdRef: ChangeDetectorRef) { }


  public printDetailsPage(): void {
    window.print();
  }
}
