import { Component, Input, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'details-who-should-apply',
  templateUrl: './who-should-apply.component.html',
  styleUrls: ['./who-should-apply.component.scss']
})
export class DetailsWhoShouldApplyComponent implements OnInit {
  setAsides: any[];
  preferences: any[];

  url: string;

  FAQs = [
    {
      question: 'Who Should Apply?',
      answer: `Individuals or households who meet the income and household
              size requirements listed in the table below may apply.Qualified
              applicants will be required to meet additional selection criteria.
              Applicants who live in New York City receive a general preference
              for apartments.`
    },
    {
      question: 'When is the Deadline?',
      answer: `Applications must be postmarked or submitted online no later than December 28, 2018. Late applications will not be considered.`
    },
    {
      question: 'What Happens After You Submit an Application?',
      answer: `After the deadline, applications are selected for review through a lottery process. If yours is selected and you appear to qualify, you will
              be invited to an interview to continue the process of determining your eligibility. Interviews are usually scheduled from 2 to 10 months
              after the application deadline. You will be asked to bring documents that verify your household size, identity of members of your
              household, and your household income.`
    },
    {
      question: 'How do I apply?',
      answer: `Apply online or through mail. To apply online, please go to nyc.gov/housingconnect. To request an application by mail, send a selfaddressed
              envelope to: Van Sinderen Plaza c/o Cornell Pace Inc., 542 Main Street 3rd Floor, New Rochelle, NY 10801. Only send
              one application per development. Do not submit duplicate applications. Do not apply online and also send in a paper application.
              Applicants who submit more than one application may be disqualified.`
    },
    {
      question: 'When is the Deadline?',
      answer: `Applications must be postmarked or submitted online no later than December 28, 2018. Late applications will not be considered.`
    }
  ];

  scrollTo(element: any) {

    const elem = document.querySelector('#' + element);
    if (elem) {
      elem.scrollIntoView({ behavior: 'smooth', block: 'center', inline: 'nearest' });
    }
  }
  
  constructor(private router: Router) { }

  ngOnInit() {
    this.url = this.router.url;
  }
}
