import { Component, ChangeDetectorRef } from '@angular/core';
import { FormControl, Validators } from "@angular/forms";
import { LotteryApiService } from '../../../../shared/services/api/lottery-api.service';
import { Router, ActivatedRoute } from '@angular/router';
import { ApplicationsApiService } from '../../../dashboard/services/applications-api.service';
import { MarkerModel } from 'projects/web/src/app/shared/models/marker.model';
import { PropertyTypeEnum, OwnerTypeEnum } from 'projects/web/src/app/shared/enums/developmentEnums';
import { ToastrService } from 'ngx-toastr';
import { OAuthService } from 'angular-oauth2-oidc';
import { Observable } from 'rxjs';

@Component({
  selector: 'lottery-apply',
  templateUrl: './lottery-apply.component.html',
  styleUrls: ['./lottery-apply.component.scss']
})
export class LotteryApplyComponent {
  constructor(
    private activatedRoute: ActivatedRoute,
    private lotteryApiService: LotteryApiService,
    private cdRef: ChangeDetectorRef,
    private toastr: ToastrService,
    private oauthService: OAuthService,
    private router: Router,

  ) { }
  /*lottery = {
  applicationDate: '123',
  status: 'in progress',
  application: 'sssss',
  lotteryDescription: '@sdasd',
  lotteryName: '@asdasd'
};*/
  lottery: any;
  lotteryId: number;
  isAgreeWithTermsOfUse: FormControl = new FormControl(null, [Validators.requiredTrue]);
  applicationSummary = {};
  appLoaded: boolean = false;
  cardViewType: string = 'full';

  ngOnInit() {
    this.lotteryId = this.activatedRoute.snapshot.params['id'];
    this.lotteryApiService.getCardView(this.lotteryId.toString()).subscribe(details => {
      this.lottery = this.aggregateApplicationData(details);
      console.log(this.lottery);
      console.log(this.lotteryId);
      this.cdRef.detectChanges();
    })

    this.lotteryApiService.getApplicationDashboardSummary().subscribe(data => {
      this.applicationSummary = data;
      console.log(data);
      this.appLoaded = true;
      if (!this.cdRef["destroyed"]) this.cdRef.detectChanges();
    });

  }

  CanIApply() {
    return Observable.create(observer => {
      if (this.isUserLoggedIn()) {
        this.lotteryApiService.CanIApply(this.lotteryId).subscribe((response: boolean) => {
          observer.next(response);
          observer.complete();
        });
      }
      else {
        observer.next(true);
        observer.complete();
      }
    });
  }
  
  isUserLoggedIn() {
    return ((this.oauthService.hasValidIdToken() && this.oauthService.hasValidAccessToken()));
  }

  private aggregateApplicationData(item) {
    const subwayServices = item.trains
      ? Array.from(new Set(item.trains.split(','))).sort()
      : [];

    const markers: Array<MarkerModel> = (item.markers)
      ? item.markers.map((item, index) => {
        return {
          address: item.address,
          city: item.city,
          lat: item.lat,
          lng: item.lng,
          label: item.name,
          markerLabel: item.name,
          state: item.state,
          zip: item.zip,
          fullAddress: `$(item.address), $(item.city), $(item.state) $(item.zip)`,
          id: 0,
          draggable: false,
          index: index,
          iconUrl: '/assets/images/map-marker.svg',
          type: 'building',
          color: null,
          trainName: null
        } as MarkerModel;
      })
      : [];

    return {
      mainImage: (item.defaultPhotoStream)
        ? (item.defaultPhotoStream)
        : '/assets/images/demo-lottery-house.png',
      markers,
      details: {
        amenities: item.amenities,
        area: (
          (item.neighborhood)
            ? item.neighborhood
            : 'Neighborhood'
        ) + ' | ' + ((item.borough) ? item.borough : 'Borough'),
        borough: item.borough,
        description: item.lotteryDescription,
        endIn: item.endIn,
        lotteryEndDate: item.lotteryEndDate,
        lotteryId: item.lotteryId,
        lotteryName: item.lotteryName,
        street: item.lotteryName,
        subwayServices,
        images: [
          (item.defaultPhotoStream) ? (item.defaultPhotoStream) : '/assets/images/demo-lottery-house.png'
        ],
        isAddedToFavorite: false,
        expirationDate: new Date(),
        expirationDateText: (item.endIn > 0)
          ? `Lottery closing in ${item.endIn} days`
          : (item.endIn === 0)
            ? 'Lottery ends today'
            : 'Lottery closed',
        minIncome: item.minIncome,
        maxIncome: item.maxIncome,
        minHousehold: item.minHouseholdSize,
        maxHousehold: item.maxHouseholdSize,
        mobilityPref: item.mobility,
        units: item.units,
        studios: item.studios,
        oneBR: item.oneBR,
        twoBR: item.twoBR,
        threeBR: item.threeBR,
        fourBR: item.fourBR,
        unitLayouts: item.unitLayouts,
        propertyTypeId: item.propertyTypeId,
        propertyType: (item.propertyTypeId == PropertyTypeEnum.Rental ? "Rental" : (item.propertyTypeId == PropertyTypeEnum.Owner ? "Sales" : "")),
        ownerTypeId: item.ownerTypeId,
        ownerType: (item.ownerTypeId == OwnerTypeEnum.SmallHome ? "Small Home" : (item.ownerTypeId == OwnerTypeEnum.CondoCoop ? "Condo/Co-op" : "")),
        visionHearingPref: item.visionHearing,
        lotteryApplication: item.lotteryApplication,
      }
    }
  }

  submit() {

    this.CanIApply().subscribe(canApply => {
      if (!canApply) {
        this.toastr.error('Already applied');
        return;
      }
      console.log('CAN APPLY');
      this.lotteryApiService.applyLottery(this.lotteryId)
        .subscribe(result => {
          if (result) {
            if (result.success) {
              this.toastr.success(result.message);              
              this.router.navigate([`/details/confirmation/` + result.id]);
            }
            else {
              this.toastr.error(result.message);
            }

            console.log(result);
          }
          else {
            this.toastr.error("Could not process the request");
          }
        });
    });
    
  }

}
