import { Component, Input, ChangeDetectorRef, OnInit, ViewChild } from '@angular/core';
import { NgxGalleryOptions, NgxGalleryImage } from '../../../ngx-gallery';

@Component({
  selector: 'details-photos',
  templateUrl: './photos.component.html',
  styleUrls: ['./photos.component.scss']
})
export class DetailsPhotosComponent implements OnInit {
  @Input() photos: string[];

  isLoading: boolean = true;
  isVisible: boolean = false;
  isSeen: boolean = false;
  @Input() galleryOptions: NgxGalleryOptions[];
  @Input() galleryImages: NgxGalleryImage[] = [];

  constructor(private cdRef: ChangeDetectorRef) { }

  @ViewChild("gallery")
  gallery: any;


  openGallery(i:number) {
    const index = i? i : 0;
    this.gallery.openPreview(index);
  }

  ngOnInit() {
    setTimeout(() => {  //fixes ExpressionChangedAfterItHasBeenCheckedError error
      if (this.photos) this.isLoading = false;
      this.cdRef.detectChanges();
    });    
  }

  handleChangingVisible(event) {
    this.isVisible = !this.isVisible;
    this.isSeen = true;
  }

  log(e) {
    console.log(e);
  }
}
