import {Component, EventEmitter, Input, Output} from '@angular/core';
import { Router } from '@angular/router';
import { OAuthService } from 'angular-oauth2-oidc';
import { LotteryApiService } from '../../../../shared/services/api/lottery-api.service';

@Component({
  selector: 'details-nav-bar',
  templateUrl: './details-nav-bar.component.html',
  styleUrls: ['./details-nav-bar.component.scss']
})
export class DetailsNavBarComponent {
  url: string;
  isToolBarVisible: boolean;
  @Input() lotteryId: string;
  @Input() canApply: boolean;
  @Output() onClickApplyBtn: EventEmitter<any> = new EventEmitter();

  handleChangingToolBarVisible(value) {
    this.isToolBarVisible = !value.isVisible;
  }

  constructor(private router: Router, private oauthService: OAuthService, private lotteryService: LotteryApiService) {
    this.url = this.router.url;
    
  }  

 scrollTo(element: any) {

    const elem = document.querySelector('#' + element);
    if (elem) {
      elem.scrollIntoView({ behavior: 'smooth', block: 'center', inline: 'nearest' });
    }
  }

  onLotteryApply(){
    this.router.navigate([`/details/${this.lotteryId}/apply-lottery`]);
  }
}
