import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { Router } from "@angular/router";
import { OAuthService } from 'angular-oauth2-oidc';
import { LotteryApiService } from '../../../../shared/services/api/lottery-api.service';

@Component({
  selector: 'details-overview',
  templateUrl: './overview.component.html',
  styleUrls: ['./overview.component.scss']
})
export class DetailsOverviewComponent implements OnInit {
  @Input() lotteryName: string;
  @Input() amenities: any[];
  @Input() endDate: string;
  @Input() units: any[];
  @Input() lotterySetAsidePreferences: any[];
  @Input() adContent: any;
  @Input() lotteryId: string;
  @Input() lotteryBuildings: any[];
  @Input() canApply: boolean;
  @Output() isApplyBtnVisible: EventEmitter<any> = new EventEmitter();
  @Output() onClickApplyBtn: EventEmitter<any> = new EventEmitter();
  buildingAmenities: any[];
  developmentAmenities: any[];

  constructor(private router: Router, private oauthService: OAuthService, private lotteryService: LotteryApiService) {

  }


  getSetAsidesPerferences(isSetAside: boolean) {
    return this.lotterySetAsidePreferences.filter(x => x.preferenceType.isSetAside == isSetAside).sort((x, y) => {
      return x.priority - y.priority;
    });
  }

  ngOnInit() {

    if (this.amenities) {
      this.buildingAmenities = this.amenities.filter(a => a.isBuildingLevel);
      this.developmentAmenities = this.amenities.filter(a => a.isDevelopmentLevel);
    }
  }

  isUserLoggedIn() {
    return ((this.oauthService.hasValidIdToken() && this.oauthService.hasValidAccessToken()));
  }

  handleChangingApplyBtnVisible() {
    this.isApplyBtnVisible.emit("toggle");
  }


  onLotteryApply() {
    this.router.navigate([`/details/${this.lotteryId}/apply-lottery`]);
  }
}
