import {Component, Input} from '@angular/core';
import {Router} from "@angular/router";
import { OAuthService } from 'angular-oauth2-oidc';
import { LotteryApiService } from '../../../../shared/services/api/lottery-api.service';

@Component({
  selector: 'details-how-to-apply',
  templateUrl: './how-to-apply.component.html',
  styleUrls: ['./how-to-apply.component.scss']
})
export class DetailsHowToApplyComponent {
  @Input() advertisementContent: any;
  @Input() lottery: any;
  @Input() lotteryId: string;
  @Input() canApply: boolean;

  constructor(private router: Router, private oauthService: OAuthService, private lotteryService: LotteryApiService) {
    
  }

  isUserLoggedIn() {
    return ((this.oauthService.hasValidIdToken() && this.oauthService.hasValidAccessToken()));
  }

  applicationWarnings = [
    'Do not submit duplicate applications.',
    'Only send one application per lottery.',
    'Do not apply online and also send in a paper application.',
    'Applicants who submit more than one application may be disqualified.'
  ];

  onApplyLottery() {
    this.router.navigate([`/details/${this.lotteryId}/apply-lottery`]);
  }
}
