import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DetailsComponent } from './details.component';
import { LotteryApplyComponent} from "./pages/lottery-apply/lottery-apply.component";
import { AuthGuard} from "../../shared/auth/auth.guard";
import { ApplicationConfirmationComponent} from "./pages/confirmation/confirmation.component";

const routes : Routes = [
  {
    path: ':id',
    component: DetailsComponent
  },
  {
    path: ':id/apply-lottery',
    component: LotteryApplyComponent,
    canActivate: [AuthGuard]
  },

  {
    path: 'confirmation/:id',
    component: ApplicationConfirmationComponent,
    canActivate: [AuthGuard]    
  },
  {
    path: '',
    redirectTo: '/search-businesses'
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DetailsRoutingModule {}
